                        <fieldset>
                            <legend> French</legend>
                            #** Problme d'optimisation financire 3 .<br><br>
                            Il s'agit d'un problme d'optimisation linaire du risque et du rendement d'un portefeuille. <br><br>
                            Notre objectif est de minimiser le risque du portefeuille tout en satisfaisant simultanment 5 contraintes:<br>
                            1. La somme des investissements sera de 100 000 $<br>
                            2. Le portefeuille a un rendement annuel d'au moins 7,5%<br>
                            3. Au moins 50% des placements sont nots A<br>
                            4. Au moins 40% des investissements sont immdiatement liquides<br>
                            5. Les comptes d'pargne et les certificats de dpt ne dpassent pas 30 000 dollars.<br><br>

                            ** Modlisation du LP :<br>
                            Pour revoir le processus de rsolution d'un problme d'optimisation linaire, il y a 3 tapes:<br>
                            1. Variables de dcision: ici, il y a 8 variables de dcision. Ce sont nos options d'investissement.<br><br>

                            2. Objectif Fonction: Nous voulons minimiser le risque pour les 8 investissements. <br>
                            Voici les placements multiplis par leurs coefficients de risque respectifs.<br>
                            0X1 + 0X2 + 25X3 + 30X4 + 20X5 + 15X6 + 65X7 + 40X8<br><br>
                            3. Contraintes: Enfin, nous voulons dfinir exactement quelles sont nos contraintes. <br>
                            Celles-ci sont exprimes algbriquement ci-dessous dans le mme ordre que nous avons numr les contraintes prcdemment:<br>
                            1. Xi = 100000<br>
                            2. .04X1+.052X2+.071X3+.1X4+.82X5+.065X6+.2X7+.125X8 >= 7500<br>
                            3. X1 + X2 + X5 + X7 >= 40000<br>
                            4. X1 + X3 + X4 + X7 + X8 >= 40000<br>
                            5. X1 + X2 <= 30000<br>
                                Au cas o vous seriez confus au sujet des 7 500 de la contrainte n  2, ce serait le rendement annuel de 7,5% que nous recherchons multipli par notre investissement de 100 000 $.
                        </fieldset>

