                {
                    "name": "Nocal Mining Bond ",
                    "expected_return": 6.5,
                    "rating": 0,
                    "risk": 15,
                    "liquidity": 0,
                    "savecd": 0,
                    "amt_invested": 1,
                    "img": "nocal.jpg"
                },
                {
                    "name": "Minocompo System",
                    "expected_return": 20.0,
                    "rating": 1,
                    "risk": 65,
                    "liquidity": 1,
                    "savecd": 0,
                    "amt_invested": 1,
