                    "img": "roi.jpg",
                    "comparator": ">",
                    "type":"percent"
                },
                 {
                    "name": "A rating ",
                    "value": 50,
                    "img": "a-rating.jpg",
                    "comparator": ">",
                    "type":"percent"
                },
                 {
                    "name": "Immediatly_liquid ",
                    "value": 40,
                    "img": "immediate-liquidity.jpg",
                    "comparator": ">",
                    "type":"percent"
                },
                 {
                    "name": "savings ",
                    "value": 30000,
                    "img": "saving.jpg",
                    "comparator": "<",
                    "type":"money"
                }

            ],
            show: true,

            PulpLpSummary: "",
            linearSolverResult: {},
            lpDescription: "Minimize your portfolio risks  !",
            img: "",
            currentPage: 0,
            pageCount: 0,
            myPage:1
        }
    },
    components: {
        VueSlider,
        RangeSlider,
        pdf
    },
    computed: {
        total: function () {
            return this.value * 10
        }
    },
    methods: {
        async test() {
            // Just an async function, for FUN 
            return "test"
        },
        getLinearSolver() {

            
            // Ugly momentaneus tactic to format expected_return from 100% to 0.100
            var items = JSON.parse(JSON.stringify(this.items)) // Breaking the 2 ways binding

            items.forEach(function(item, index){
                this[index].expected_return = parseFloat((this[index].expected_return / 100).toFixed(3))
            }, items);


            var data = {
                "items": items,
                "constraints": JSON.parse(JSON.stringify(this.constraints)) // Breaking the 2 ways binding
            }

            // Pulp Ajax Query , using the formatted good data 
            this.formatDataForPulp(data).then((res) => {

                const path = '/api/optimize_portfolio_1';

                axios.post(path, res)

                    .then((res) => {
                        // Getting the PULP linear solver result
                        this.linearSolverResult = res.data;

                        // Designing the PULP Lp desc coming from pulp
                        this.PulpLpSummary = this.linearSolverResult.PulpLpSummary;
                        this.PulpLpSummary.replace(new RegExp('\r?\n', 'g'), '<br />');

                        // Removing the PulpLpSUmmary from the main JSON result
                        delete this.linearSolverResult.PulpLpSummary;
                    })
                    .catch((error) => {
                        // eslint-disable-next-line
                        console.error(error);
                    });

            })
        },
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        onSubmit(evt) {
            evt.preventDefault()
            this.getLinearSolver()
        },
        reset() {

            location.reload()
            // evt.preventDefault()
            // Reset our form values TODO

            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
                this.show = true
            })
        }
    },
    filters: {
        capitalize: function (value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    created() {
        // Getting the shared function 
        this.formatDataForPulp = shared.formatDataForPulp.bind(this) // now you can call this.foo() (in your functions/template)
    }
}
</script>

